import React, { useContext } from "react";
import styled from "styled-components";
import ThemeContext from "../../theme/ThemeContext";
import ControlsContext from "../../ControlsContext";

const Circle = styled.circle`
  fill: ${(props) => props.theme.use.shellAndProg.bg};
  stroke: ${(props) => (props.launched ? "green" : "rgb(243,90,90)")};
`;

const ProgressRing = (props) => {
  const theme = useContext(ThemeContext);
  const [stages] = useContext(ControlsContext);
  const { radius, stroke, progress } = props;
  let normalizedRadius = radius - stroke * 2;
  let circumference = normalizedRadius * 2 * Math.PI;
  const strokeDashoffset = circumference - (progress / 100) * circumference;

  return (
    <svg height={radius * 2} width={radius * 2}>
      <Circle
        launched={stages.fire ? true : false}
        theme={theme}
        strokeWidth={stroke}
        strokeDasharray={circumference + " " + circumference}
        style={{ strokeDashoffset }}
        r={normalizedRadius}
        cx={radius}
        cy={radius}
      />
    </svg>
  );
};

export default ProgressRing;
