import React, { useState, useEffect, useContext, Fragment } from "react";
import styled, { keyframes } from "styled-components";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import ControlsContext from "../ControlsContext";
import eightyAudio from "../assets/eighty.mp3";
import fiveAudio from "../assets/five.mp3";
import nintyAudio from "../assets/ninty.mp3";
import tfiveAudio from "../assets/tfive.mp3";
import { Percentage } from "../LogContext";

const fly = keyframes`
    0% {height:5px;transform:translateY(0px);background:#9b59b6;}
    25% {height:20px;transform:translateY(8px);background:#3498db;}
    50% {height:5px;transform:translateY(0px);background:#9b59b6;}
    100% {height:5px;transform:translateY(0px);background:#9b59b6;}
`;
const Signal = styled.div`
  position: absolute;
  right: 15%;
  top: 7%;
  color: red;
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
`;
const Wave = styled.div`
  position: relative;
  margin-left: 0.5rem;
  visibility: ${(props) => (props.showWave ? "visibile" : "hidden")};
  & span {
    display: block;
    bottom: 0px;
    width: 3px;
    height: 4px;
    background: #9b59b6;
    position: absolute;
    animation: ${fly} 1.1s infinite ease-in-out;
  }
  & span:nth-child(2) {
    left: 11px;
    animation-delay: 0.2s;
  }

  & span:nth-child(3) {
    left: 22px;
    animation-delay: 0.4s;
  }
`;
const Audio = styled.audio`
  visibility: hidden;
  position: fixed;
`;
const MyFontAwsm = styled(FontAwesomeIcon)`
  margin: 0 0.5rem;
  font-size: 1.5rem;
  color: rgb(89, 89, 247);
  &:hover {
    background-color: transparent;
    cursor: pointer;
  }
`;

const AudioSignal = () => {
  const [percenatge] = useContext(Percentage);
  const [showWave, setShowWave] = useState(false);
  const [stages] = useContext(ControlsContext);
  const [audioContent, setAudioContent] = useState("");

  const fetchAudioTrack = (track) => {
    // get audio tracks
    fetch(track, { cache: "no-store" })
      .then((resp) => {
        if (resp.status === 200) {
          return resp.blob();
        }
      })
      .then((blob) => {
        let content = URL.createObjectURL(
          new Blob([blob], { type: "audio/mp3" })
        );
        setAudioContent(content);
      });
  };

  useEffect(() => {
    if (stages.start) {
      // check percentage range
      if (percenatge === 2) {
        fetchAudioTrack(eightyAudio);
      }
      if (percenatge === 50) {
        fetchAudioTrack(nintyAudio);
      }
      if (percenatge === 70) {
        fetchAudioTrack(fiveAudio);
      }
      if (percenatge === 90) {
        fetchAudioTrack(tfiveAudio);
      }
    }
  }, [stages.start, percenatge]);

  // show microphone sound wave on video play
  useEffect(() => {
    if (stages.fire) {
      document.querySelector("#vid").addEventListener("play", () => {
        if (showWave !== true) setShowWave(true);
      });

      document.querySelector("#vid").addEventListener("ended", () => {
        if (showWave !== false) setShowWave(false);
      });
    }
  }, [stages.fire, showWave]);

  useEffect(() => {
    if (audioContent !== "") {
      setShowWave(true);
      document.querySelector("#audio-player").play();
    }

    document.querySelector("#audio-player").addEventListener("ended", () => {
      setShowWave(false);
    });
  }, [audioContent]);

  return (
    <Fragment>
      <Audio id="audio-player" src={audioContent}></Audio>
      <Signal>
        <MyFontAwsm icon="headset" />
        <Wave showWave={showWave}>
          <span></span>
          <span></span>
          <span></span>
        </Wave>
      </Signal>
    </Fragment>
  );
};

export default AudioSignal;
