import React, { useState, useEffect, useContext } from "react";
import vid from "../assets/videoo.mp4";
import styled, { keyframes } from "styled-components";
import ControlsContext from "../ControlsContext";
import { LogContext } from "../LogContext";
import { Percentage } from "../LogContext";
import ThemeContext from "../theme/ThemeContext";

const bubbling = keyframes`
  from{
    top: -4px;
  } 
  to{
    top: -1px;
  }
`;
const ProgressBar = styled.div`
  height: ${(props) => props.loaded}%;
  text-align: center;
  background-color: rgb(89, 89, 247);
  position: relative;
  & .bubble-left {
    background: rgb(89, 89, 247);
    width: 49px;
    height: 26px;
    border-radius: 50%;
    position: absolute;
    top: -4px;
    left: -10px;
    animation: 0.5s ${bubbling} linear 0.3s infinite alternate;
  }
  & .bubble-right {
    background: rgb(89, 89, 247);
    width: 48px;
    height: 26px;
    border-radius: 50%;
    position: absolute;
    top: -4px;
    right: -10px;
    animation: 0.5s ${bubbling} linear infinite alternate;
  }
`;
const ProgressContainer = styled.div`
  width: 45px;
  height: 90%;
  display: flex;
  flex-direction: column-reverse;
  background-color: ${(props) => props.theme.use.shellAndProg.bg};
  align-self: center;
  margin: 0 0.5rem;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  overflow: hidden;
`;
const Precentage = styled.div`
  color: rgb(89, 89, 247);
  text-align: center;
  padding: 0.5rem;
`;

const Progress = () => {
  const theme = useContext(ThemeContext);
  // eslint-disable-next-line
  const [stages, dispatch] = useContext(ControlsContext);
  const [precentage, setPrecentage] = useState(0);
  // eslint-disable-next-line
  const [log, setLog] = useContext(LogContext);
  // eslint-disable-next-line
  const [sendPercentageToAudio, setSendPercentageToAudio] = useContext(
    Percentage
  );
  const downloadProg = async () => {
    // resp and reader
    /**
     * here i forced no cach when making the request to the app in order to keep the exprience look neat
     */
    let response = await fetch(vid, {
      cache: "no-store",
    });
    let reader = response.body.getReader();

    // get total content length
    let totalLength = +response.headers.get("Content-Length");

    // start reading
    let received = 0;
    let chunks = [];
    const updateWithDataProgress = (v) => {
      if (v !== Math.round((received / totalLength) * 100)) {
        return Math.round((received / totalLength) * 100);
      } else {
        return v;
      }
    };

    while (true) {
      // extract chunks or finish
      const { done, value } = await reader.read();

      if (done) {
        // end loop
        break;
      }
      setLog((log) => {
        if (value.toString() !== log) return value.slice(0, 3).toString();
        return log;
      });
      // save indivisual chunks
      chunks.push(value);
      // update total recevied so far
      received += value.length;
      // update percentage for progress bar in the UI
      let update = updateWithDataProgress(precentage);
      setPrecentage(update);
      setSendPercentageToAudio(update);
    }

    let chunksAll = new Uint8Array(received);
    let position = 0;

    for (let chunk of chunks) {
      chunksAll.set(chunk, position);
      position += chunk.length;
    }

    let blob = new Blob([chunksAll], { type: "video/mp4" });
    let content = URL.createObjectURL(blob);

    // this dispatch leads to 'fire'..it sets satges.ready to true

    dispatch({
      type: "READY",
      payload: {
        vidData: content,
      },
    });

    // delete url object from memeory
    return () => {
      URL.revokeObjectURL(content);
    };
  };

  useEffect(() => {
    if (stages.start) {
      downloadProg();
    }
    // eslint-disable-next-line
  }, [stages.start]);

  // reset fule tank/downlaod progress
  useEffect(() => {
    if (stages.ready) {
      document.querySelector("#vid").addEventListener("ended", () => {
        setPrecentage(0);
      });
    }
  }, [stages.ready]);

  return (
    <div>
      <ProgressContainer id="progress-bar-itself-container" theme={theme}>
        <ProgressBar id="progress-bar-itself" loaded={precentage}>
          <div className="bubble-left"></div>
          <div className="bubble-right"></div>
        </ProgressBar>
      </ProgressContainer>
      <Precentage>{precentage}%</Precentage>
    </div>
  );
};

export default Progress;
