import React, { useContext, Fragment } from "react";
import ScreenComp from "../../comps/ScreenComp";
import styled from "styled-components";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import ThemeContext from "../../theme/ThemeContext";

const MyFontAwsm = styled(FontAwesomeIcon)`
  margin: 0 0.5rem;
  font-size: 1.5rem;
  color: ${(props) => props.theme.settingsIcons.bg};
  &:hover {
    background-color: transparent;
    cursor: pointer;
  }
`;
const SettingsOptions = styled.div`
  width: 100%;
  margin-left: 1rem;
  display: flex;
  padding: 1rem;
  justify-content: space-around;
  align-items: center;
`;

const Settings = () => {
  const { use, setTheme } = useContext(ThemeContext);
  return (
    <Fragment>
      <ScreenComp isCameraView={false} isLog={true}>
        <SettingsOptions>
          <MyFontAwsm
            theme={use}
            icon="sun"
            onClick={(e) => {
              import("../../theme/theme").then(({ light }) => {
                setTheme(light);
              });
            }}
          />
          <MyFontAwsm
            theme={use}
            icon="moon"
            onClick={(e) => {
              import("../../theme/theme").then(({ dark }) => {
                setTheme(dark);
              });
            }}
          />
          <MyFontAwsm
            theme={use}
            icon="compress"
            onClick={(e) => {
              if (document.fullscreenEnabled) {
                if (!document.fullscreenElement) {
                  document.documentElement.requestFullscreen();
                } else {
                  document.exitFullscreen();
                }
              } else {
                alert(
                  "Fullscreen is not supported on " +
                    navigator.userAgent +
                    "try the app on a diffrent browser"
                );
              }
            }}
          />

          <a href="https://www.imustafa.com" target="_new">
            <MyFontAwsm theme={use} icon="user-astronaut" />
          </a>
        </SettingsOptions>
      </ScreenComp>
    </Fragment>
  );
};

export default Settings;
