import React, { useContext, useEffect, useRef } from "react";
import styled from "styled-components";
import { LogContext } from "../../LogContext";
import ScreenComp from "../../comps/ScreenComp";
import ControlsContext from "../../ControlsContext";
import ThemeContext from "../../theme/ThemeContext";

const ShellLike = styled.div`
  background-color: ${(props) => props.theme.shellAndProg.bg};
  align-self: center;
  margin: 0 0.5rem;
  padding: 1rem;
  height: 100%;
  width: 100%;
  color: ${(props) => props.theme.shellAndProg.color};
  overflow: auto;
  /* add scroll bar styles here  */
  ::-webkit-scrollbar {
    display: none;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    display: none;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    display: none;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    display: none;
  }
`;

const LogData = () => {
  const { use } = useContext(ThemeContext);
  // eslint-disable-next-line
  const [log, setLog] = useContext(LogContext);
  // eslint-disable-next-line
  const [stages, dispatch] = useContext(ControlsContext);
  const shell = useRef();

  useEffect(() => {
    let el = document.createElement("p");
    el.innerHTML = "_> " + log;
    shell.current.append(el);
    el.scrollIntoView();
  }, [log]);

  useEffect(() => {
    if (stages.start === true && stages.ready !== true) {
      let el = document.createElement("p");
      el.innerHTML = "_> <span style=color:red>initializing</span>";
      shell.current.append(el);
      el.scrollIntoView();
    }

    if (stages.ready === true) {
      let el = document.createElement("p");
      el.innerHTML =
        "<p>_> <span style=color:green> succesfull</span></p> <p>_> launch now</p>";
      shell.current.append(el);
      el.scrollIntoView();

      // wtach for vid end to reset shell screen
      document.querySelector("#vid").addEventListener("ended", () => {
        shell.current.innerHTML = "_>";
      });
    }
  }, [stages.start, stages.ready]);

  return (
    <ScreenComp isCameraView={false} isLog={true}>
      <ShellLike theme={use} ref={shell} id="shell"></ShellLike>
    </ScreenComp>
  );
};
export default LogData;
