import React, { useContext } from "react";
import styled from "styled-components";
import ThemeContext from "../theme/ThemeContext";

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: ${(props) => (props.isCameraView ? "75%" : "90%")};
  border: 1px solid ${(props) => props.theme.ScreenComp.border};
  padding: 0.5rem;
  border-radius: 10px;
  margin: ${(props) => (props.isCameraView ? "0.8rem 0" : "0.5rem auto")};
  max-height: ${(props) =>
    props.isCameraView ? "100%" : props.isLog ? "100%" : "23.333333vh"};
`;
const UpperFrame = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`;
const UpperFrameLine = styled.div`
  background-color: ${(props) => props.theme.ScreenComp.FrameLine.bg};
  height: 6px;
  border-radius: 10px;
  flex-grow: 2;
  margin-right: auto;
`;
const DotOne = styled.div`
  padding: 4px;
  background-color: rgb(243, 90, 90);
  border-radius: 50%;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
`;
const DotTwo = styled.div`
  padding: 4px;
  background-color: rgb(89, 89, 247);
  border-radius: 50%;
`;
const Content = styled.div`
  width: 100%;
  padding: ${(props) => (props.isCameraView ? "0.5rem" : "0")};
  overflow: hidden;
  margin-top: 0.5rem;
  border-radius: 10px;
  background: ${(props) => (props.isCameraView ? "transparent" : "inherit")};
  display: flex;
  height: ${(props) =>
    props.isCameraView === true
      ? "100%"
      : props.isLog
      ? "100%"
      : `${100 / 3.5}vh`};
  overflow: ${(props) => (props.isLog ? "scroll" : "hidden")};
  position: relative;
  /* add scroll bar styles here  */
  ::-webkit-scrollbar {
    display: none;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    display: none;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    display: none;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    display: none;
  }
`;

const ScreenComp = ({ children, isCameraView, isLog }) => {
  const { use } = useContext(ThemeContext);
  return (
    <Wrapper isCameraView={isCameraView} isLog={isLog} theme={use}>
      <UpperFrame>
        <UpperFrameLine theme={use} />
        <DotOne />
        <DotTwo />
      </UpperFrame>
      <Content isCameraView={isCameraView} isLog={isLog}>
        {children}
      </Content>
    </Wrapper>
  );
};

export default ScreenComp;
