import React, { useContext, useRef, useEffect } from "react";
import ControlsContext from "./ControlsContext";
import styled, { keyframes } from "styled-components";
import ScreenComp from "./comps/ScreenComp";
import LiveImg from "./assets/live.jpg";

const signaling = keyframes`
  from {
    background-color: #ff00008f;
  }
  to{
    background-color: red;
  }
`;
const Video = styled.video`
  @media (min-width: 1500px) {
    width: 1800px;
    position: absolute;
    left: -200px;
  }
  @media (max-width: 1500px) {
    width: 1290px;
    position: absolute;
    left: -200px;
  }
`;
const Display = styled.div`
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
  width: 100%;
  background-color: #303030;
  border-radius: 10px;
  position: relative;
  background-image: url(${LiveImg});
  z-index: ${(props) => (props.start === "true" ? "1" : "-1")};
`;
const LiveStatus = styled.div`
  z-index: 3000;
  position: absolute;
  right: 5%;
  top: 5%;
  padding: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  & .live {
    color: white;
    font-weight: 900;
    font-size: 1.5rem;
  }
  & .signal {
    margin-right: 0.5rem;
    padding: 0.8rem;
    border-radius: 50%;
    background-color: red;
    animation: 0.5s ${signaling} infinite;
    animation-direction: alternate;
  }
`;

const Screen = () => {
  // eslint-disable-next-line
  const [stages, dispatch] = useContext(ControlsContext);
  const vidRef = useRef();

  // make the vid play();
  useEffect(() => {
    if (stages.ready && stages.fire) {
      vidRef.current.play();
    }
  }, [stages.fire, stages.ready]);

  return (
    <ScreenComp isCameraView={true}>
      <Display start={stages.start ? "true" : "false"}>
        {stages.start && (
          <LiveStatus>
            <div className="signal"></div>
            <div className="live">Live</div>
          </LiveStatus>
        )}

        {stages.ready && (
          <Video
            id="vid"
            ref={vidRef}
            onContextMenu={(e) => e.preventDefault()}
          >
            <source src={stages.vidData} type="video/mp4"></source>
          </Video>
        )}
      </Display>
    </ScreenComp>
  );
};
export default Screen;
