import React from "react";
import styled from "styled-components";
import Buttons from "./board/buttons/Buttons";
import Log from "./board/log/Log";
import Settings from "./board/Contact/Settings";

const Controls = styled.div`
  padding: 0.5rem;
  min-width: 300px;
  width: 25%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Board = () => {
  return (
    <Controls>
      <Buttons />
      <Log />
      <Settings />
    </Controls>
  );
};
export default Board;
