import React, { useReducer, useState, useContext } from "react";
import ControlStation from "./ControlStation";
import ControlsContext from "./ControlsContext";
import { LogContext } from "./LogContext";
import { Percentage } from "./LogContext";
import styled, { createGlobalStyle } from "styled-components";
import ThemeContext from "./theme/ThemeContext";
import { dark } from "./theme/theme";
import { library } from "@fortawesome/fontawesome-svg-core";
import { fab, faLinkedin, faRedhat } from "@fortawesome/free-brands-svg-icons";
import {
  faSun,
  faMoon,
  faCompress,
  faVolumeMute,
  faUserAstronaut,
  faHeadset,
} from "@fortawesome/free-solid-svg-icons";
library.add(
  fab,
  faLinkedin,
  faRedhat,
  faSun,
  faMoon,
  faCompress,
  faVolumeMute,
  faUserAstronaut,
  faHeadset
);

const Globals = createGlobalStyle`

* {
  box-sizing: border-box !important;
}

body,
html,
#root {
  font-family: monospace;
  margin: 0;
  padding: 0;
  height: 100vh;
  background-color: ${(props) => props.theme.use.body.bg}
}

* :focus {
  outline: none;
}

circle {
  transition: stroke-dashoffset 0.35s;
  transform: rotate(-90deg);
  transform-origin: 50% 50%;
}


`;
const WrapGlobals = () => {
  const theme = useContext(ThemeContext);
  return <Globals theme={theme} />;
};
const initialState = {
  start: false,
  ready: false,
  fire: false,
  vidData: null,
  stateText: "START",
};

const reducer = (state, action) => {
  if (action.type === "START") {
    return {
      ...state,
      start: true,
      stateText: "Injecting",
    };
  }
  if (action.type === "READY") {
    return {
      ...state,
      ready: true,
      stateText: "Fire",
      vidData: action.payload.vidData,
    };
  }
  if (action.type === "FIRE") {
    return {
      ...state,
      stateText: action.payload.text,
      fire: action.payload.fire,
    };
  }

  if (action.type === "REPEAT") {
    return initialState;
  }

  return state;
};

const GoGetYourSelfPC = styled.div`
  display: none;
  @media (max-width: 1200px) {
    display: flex;
    width: 100vw;
    height: 100vh;
    margin: 0;
    padding: 0;
    justify-content: center;
    align-items: center;
    color: white;
  }
`;

const App = () => {
  const stages = useReducer(reducer, initialState);
  const log = useState("");
  const [use, setTheme] = useState(dark);
  const percentage = useState(0);
  return (
    <ControlsContext.Provider value={stages}>
      <LogContext.Provider value={log}>
        <Percentage.Provider value={percentage}>
          <ThemeContext.Provider value={{ use, setTheme }}>
            <WrapGlobals />
            <GoGetYourSelfPC>
              A Space Shuttle needs a computer monitor to fire it!!
            </GoGetYourSelfPC>
            <ControlStation></ControlStation>
          </ThemeContext.Provider>
        </Percentage.Provider>
      </LogContext.Provider>
    </ControlsContext.Provider>
  );
};

export default App;
