import React from "react";
import LogData from "./LogData";
import Progress from "../../comps/progress";
import ScreenComp from "../../comps/ScreenComp";
import styled from "styled-components";

const LogAndProgressContainer = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0.8rem;
`;

const Log = () => {
  return (
    <ScreenComp isCameraView={false}>
      <LogAndProgressContainer>
        <LogData></LogData>
        <Progress />
      </LogAndProgressContainer>
    </ScreenComp>
  );
};

export default Log;
