import React, { useState, useContext, useEffect } from "react";
import ProcessRing from "./ProgressRing";
import styled from "styled-components";
import ControlsContext from "../../ControlsContext";
import ThemeContext from "../../theme/ThemeContext";

const Message = styled.span`
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: ${(props) => props.theme.use.shellAndProg.color};
  &:hover {
    cursor: pointer;
  }
`;
const Wrapper = styled.button`
  width: 50%;
  margin: auto;
  position: relative;
  display: flex;
  justify-content: center;
  outline: none;
  border: none;
  background: none;
  transform: scale(${(props) => (props.btnStyle ? 0.9 : 1)});
  &:hover {
    outline: none;
    background: none;
  }
`;

const ControlButton = () => {
  const theme = useContext(ThemeContext);
  // eslint-disable-next-line
  const [stages, dispatch] = useContext(ControlsContext);
  const [inc, setInc] = useState(0);
  const [btnStyle, setBtnStyle] = useState(false);
  const styleBtn = () => {
    // style btn
    setBtnStyle(true);
    // style btn back
    setTimeout(() => {
      setBtnStyle(false);
    }, 100);
  };
  const handleFiring = () => {
    // fire
    if (stages.ready) {
      dispatch({
        type: "FIRE",
        payload: {
          text: "Launched",
          fire: true,
        },
      });
    } else {
      if (inc === 100) {
        // when inc === 100
        return;
      }
      // on first load
      if (stages.start === false) {
        dispatch({
          type: "START",
        });
        setInc(inc + 50);
      }
    }
  };
  useEffect(() => {
    // lead button to 'fire' when vid donwload finishes
    if (stages.ready) {
      setInc(100);
      // watch for vid end to prompt repeat
      document.querySelector("#vid").addEventListener("ended", () => {
        // reset button progress
        setInc(0);
        // reste all app state
        dispatch({
          type: "REPEAT",
        });
      });
    }
  }, [stages.ready, dispatch]);

  return (
    <Wrapper
      btnStyle={btnStyle}
      disabled={stages.start === true && stages.ready === false && true}
      onClick={() => {
        styleBtn();
        handleFiring();
      }}
    >
      <Message theme={theme}>{stages.stateText}</Message>
      <ProcessRing radius={50} stroke={5} progress={inc} />
    </Wrapper>
  );
};

export default ControlButton;
